import React, { Component } from 'react';

class Listmakanan extends Component {
	constructor(props){
		super(props)
		this.state = {
			datalist: this.props.gambar
		};
	}
	render(){
		return(
			<div>
				<img src={this.state.datalist} alt="Product Makanan" width="150"/>
			</div>
			)
	}
}

export default Listmakanan;